/*=============================
	1. Variable CSS
===============================*/
:root {
  --thm-body-font: "Krub", sans-serif;
  /* --thm-body-font: var(--font-krub); */
  --thm-heading-font: "Rubik", sans-serif;
  /* --thm-heading-font: var(--font-rubik); */

  --thm-primary: #004b83;
  --thm-primary-rgb: 0, 75, 131;

  --thm-secondary: #66a3cc; /* You can change this too if needed */
  --thm-secondary-rgb: 102, 163, 204;

  --thm-body-font-color: #60666c;
  --thm-paragraph-color: #60666c;
  --thm-body-font-size: 16px;
  --thm-body-font-weight: 400;
  --thm-heading-font-color: #004b83;
  --thm-heading-font-weight: 700;
  --thm-body-background: #ffffff;
  --thm-bg-one: #004b83;
  --thm-bg-two: #66a3cc;
  --thm-white: #ffffff;
  --thm-black: #004b83;
  --thm-blue: #0d6efd;
  --thm-indigo: #6610f2;
  --thm-purple: #6f42c1;
  --thm-pink: #d63384;
  --thm-red: #dc3545;
  --thm-orange: #fd7e14;
  --thm-yellow: #ffc107;
  --thm-green: #198754;
  --thm-teal: #20c997;
  --thm-cyan: #0dcaf0;
}

body {
  font-family: var(--thm-body-font);
  font-weight: var(--thm-body-font-weight);
  font-size: var(--thm-body-font-size);
  line-height: 28px;
  color: var(--thm-body-font-color);
  font-style: normal;
}

p {
  font-size: var(--thm-body-font-size);
  font-weight: var(--thm-body-font-weight);
  line-height: 28px;
  color: var(--thm-paragraph-color);
  margin-bottom: 0;
}

ul,
li {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

img,
.img {
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
}

a,
button {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  text-decoration: none;
}

a:focus,
.btn:focus,
button:focus {
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

a:hover,
.portfolio-cat a:hover,
.footer -menu li a:hover {
  color: var(--thm-primary-color);
  text-decoration: none;
}

a,
button {
  color: var(--thm-primary-color);
  outline: medium none;
  text-decoration: none;
}

.btn:focus,
button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
  outline: 0;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--thm-heading-font);
  color: var(--thm-heading-font-color);
  margin-top: 0px;
  margin-bottom: 0;
  font-style: normal;
  line-height: 1.2em;
  font-weight: var(--thm-heading-font-weight);
  text-transform: inherit;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

/*== Swiper Nav Style1 Css ======*/
.swiper-nav-style1 {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  top: 162px;
  left: 0;
  right: 0;
  max-width: 1320px;
  width: 100%;
  padding: 0px 15px 0px;
  margin: 0 auto;
  height: 40px;
}

.swiper-nav-style1 .swiper-button-next,
.swiper-nav-style1 .swiper-button-prev {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 40px;
  border-radius: 20px;
  background-color: transparent;
  color: var(--thm-white);
  font-size: 20px;
  font-weight: 400;
  border: 1px solid var(--thm-white);
  outline: none;
  transition: all 200ms linear;
  transition-delay: 0.1s;
}

.swiper-nav-style1 .swiper-button-next {
  margin-left: 10px;
}

.swiper-nav-style1 .swiper-button-next i,
.swiper-nav-style1 .swiper-button-prev i {
  font-weight: 400;
}

.swiper-nav-style1 .swiper-button-next:hover,
.swiper-nav-style1 .swiper-button-prev:hover {
  background: var(--thm-primary);
  border-color: var(--thm-primary);
  color: #ffffff;
}

.swiper-button-next,
.swiper-container-rtl .swiper-button-prev {
  right: 0px;
  left: auto;
}

.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  left: 0px;
  right: auto;
}

.swiper-nav-style1 .swiper-button-next::after,
.swiper-nav-style1 .swiper-button-prev::after {
  display: none;
}

