/*=============================
	Thm Btn
===============================*/
.thm-btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  padding-top: 10px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 10px;
  background-color: transparent;
  color: #fff;
  font-size: 16px;
  line-height: 40px;
  font-weight: 700;
  text-transform: capitalize;
  font-family: var(--thm-body-font);
  border-radius: 4px;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
  z-index: 2;
}

.thm-btn:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  opacity: 1;
  transform: scaleX(1);
  transition-duration: 800ms;
  background-color: var(--thm-primary);
  z-index: -1;
}

.thm-btn:hover:after {
  transform: scaleX(0);
  transition-duration: 1500ms;
}

.thm-btn:before {
  position: absolute;
  top: 0px;
  left: 100%;
  right: -50px;
  content: "";
  background: var(--thm-secondary);
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  border-radius: 0%;
  transition-duration: 800ms;
  z-index: 1;
  bottom: 0;
}

.thm-btn:hover:before {
  left: 0px;
  right: 0px;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.thm-btn .txt {
  position: relative;
  display: inline-flex;
  align-items: center;
  z-index: 1;
}

.thm-btn:hover,
.thm-btn:focus {
  color: var(--thm-white);
  -webkit-box-shadow: 2px 2px 20px 0px rgba(var(--thm-black-rgb), 0.1);
  -moz-box-shadow: 2px 2px 20px 0px rgba(var(--thm-black-rgb), 0.1);
  box-shadow: 2px 2px 20px 0px rgba(var(--thm-black-rgb), 0.1);
}

.thm-btn i {
  position: relative;
  top: 0px;
  display: inline-block;
  margin-left: 7px;
  font-size: 16px;
  z-index: 1;
}

